<h1 id="layered-architecture-in-node-js" tabindex="-1"><a class="header-anchor" href="#layered-architecture-in-node-js" aria-hidden="true">#</a> Layered Architecture in Node.js</h1>
<p>How should applications with a large code base be structured so it's:</p>
<ul>
<li>Easy to understand how it's been structured.</li>
<li>Easy to add new features to it.</li>
<li>Easy to test the code in it.</li>
<li>Etc.</li>
</ul>
<p>? Well, using a layered architecture seems like a good solution, let's take a look at what that is and how we can use it in a Node.js application.</p>
